window.onload = function(){
    document.getElementById("open-menu").addEventListener("click",open=>{
        document.getElementById("navbar-container-two").style.width = "310px"; 
        document.getElementById("navbar-container-two").style.left = "0"; 
    })
    document.getElementById("close-menu").addEventListener("click",close=>{
        document.getElementById("navbar-container-two").style.width = "0";
        document.getElementById("navbar-container-two").style.left = "-40px";
    })
}